@import '../variables';

.categories {
  ul {
    display: flex;

    li {
      background-color: #f9f9f9;
      padding: 13px 30px;
      border-radius: 30px;
      margin-right: 10px;
      font-weight: bold;
      cursor: pointer;
      transition: background-color 0.1s ease-in-out;
      @include noselect();

      &:hover {
        background-color: darken(#f9f9f9, 2%);
      }

      &:active {
        background-color: darken(#f9f9f9, 5%);
      }

      &.active {
        background-color: #282828;
        color: #fff;
      }
    }
  }

  @media (max-width: 1260px) {
    width: 100%;
    overflow-X: scroll;
    ul {
    width: 785px;
    padding: 5px 0;
    }
  }
  @media (max-width: 768px) {
    ul li{
      padding: 8px 20px;
    }
  }
}
